html {
	height: 100%;
	width: 100%;
}

body {
	background-color: #282c34;
	color: white;
	height: 100%;
	width: 100%;
}	

.linkD {
	text-decoration: none;
}

.linkD:link {
	color: rgb(231, 158, 23);
}

.linkD:visited {
	color: inherit;
}

.main-container {
	display: flex;
	flex-direction: column;
	align-content: center;
	text-align: center;
	height: 100%;
}

.container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-content: center;
	text-align: center;
}

.cart-main-container {
	display: flex;
	flex-direction: column;
	padding-top: 5%;
	align-content: center;
	text-align: center;
	height: 100%;
}

.horizontal-container {
	display: flex;
	justify-content: center;
	align-content: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.nav-links {
	display: flex;
	justify-content: space-around;
	align-content: space-around;
	column-gap: 18px;
}

.header {
	display: flex;
	justify-content: space-around;
	align-content: space-around;
	align-items: baseline;
	background-color: rgb(89, 133, 134);
}

.shop-header {
	display: flex;
	justify-content: space-around;
	align-content: center;
	align-items: baseline;
	align-self: center;
	width: 750px;
	column-gap: 10px;
}

.shop-container {
	display: grid;
	grid-template-columns: repeat(5, 150px);
	align-self: center;
	grid-gap: 1px;
}

.shop-item {
	outline: 1px solid rgb(255, 255, 255);
	padding-bottom: 20px;
}

.cart-container {
	display: grid;
	grid-template-columns: repeat(2, 200px);
	align-items: baseline;
	align-self: center;
	outline: 1px solid rgb(255, 255, 255);
}

.small {
	width: 40px;
	height: 20px;
	text-align: center;
}

.blue-button {
	background-color: cornflowerblue;
	color: white;
	font-size: large;
	border: none;
	cursor: pointer;
}

.green-button {
	background-color: rgb(49, 161, 100);
	color: white;
	font-size: medium;
	border: none;
	cursor: pointer;
}

.red-button {
	background-color: rgb(179, 60, 60);
	color: white;
	font-size: medium;
	border: none;
	cursor: pointer;
	padding: 6px;
}